// container
$container-width: 75em;
$body-bg: #eee;
$module-bg: rgba(255,255,255,0.6);
$module-hover-bg: rgba(197,197,197,0.4);

//Basic stylings
$br: 4px;
$xs-pad: 4px;
$sm-pad: 8px;
$md-pad: 1.2em;
$lg-pad: 1.5em;
$xlg-pad: 3em;
$trans: .3s;

//Breakpoints
$small-breakpoint: 400px;
$large-breakpoint: $container-width;

//Colors
$primary: #2196f3;
$accent: #64ffda;
$red: #f44336;
$yellow: #ffeb3b;
$grey: #efefef;
$white: #fff;
$black: #000;
$black-light: #555;

//Text
$text: $black-light;
$secondary: rgba(0, 0, 0, .54);
$disabled: rgba(0, 0, 0, .38);
$dividers: rgba(0, 0, 0, .12);

//Links
$link-color: $text;
$link-hover-color: darken($link-color, 20%);

//Font
$sans-serif: "Roboto-Regular", Roboto, Lato, -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", "Hiragino Sans GB", "Segoe UI", "Microsoft YaHei", "微软雅黑", sans-serif;
$serif: Georgia, Times, 'Times New Roman', serif;
$monospace-font-family: Menlo, Monaco, Consolas, 'Courier New', monospace;
$font-family: $sans-serif;

//Typography
$font-size-base: 14px;
$font-size-large: 1.5em;
$font-size-small: .8em;
$font-size-h1: 5rem;
$font-size-h2: 4rem;
$font-size-h3: 3rem;
$font-size-h4: 2em;
$font-size-h5: $font-size-base;
$font-size-h6: $font-size-small;
$line-height-base: 1.4;

$border: 1px solid $dividers;



